import React from "react";

function LoungeRegisterInput({registerText}){

    return (
        <>
             <div className="lounge__input">
                       <div className="lounge__input__nameAndPassword"> 
                         <label htmlFor='nickname'></label>
                            <input type='text' id='nickname' placeholder=" "></input>
                         <label htmlFor='password'></label>    
                            <input type='password' id='password' placeholder=" "></input>
                        </div>

                        <div className="lounge__input__text__register">
                            <label htmlFor="text"></label>
                            <input type="text" placeholder=" " id="text"></input>
                            <button type="button" onClick={registerText}></button>
                        </div>
                    </div>
        
        </>
    )

}

export default LoungeRegisterInput