import '../styles/Header.css'
import React, {useState} from 'react'
import homeIcons from "../HomeIconsData";
import { useNavigate, useLocation } from "react-router-dom"


function Header({}){

    const navigate = useNavigate()
    const moveToHomePage = () => { //  X   
        navigate('/')
    }
      const iconText = useLocation()  // Icon  useNavigate state (icon.title) Sidebar  state(icon.title) iconText  
    //   console.log(iconText.state) //  Icon  useNavigate state 
    //   console.log(iconText.state.sidebarText) // Sidebar  state
    const icon = homeIcons.filter(i => iconText.pathname === i.url)

    return(
        <div className="header">
            <h5>{icon.length !== 0 && icon[0].iconTitle}</h5> 
            <div className='button-container'>
                <button onClick={moveToHomePage}>X</button>
            </div>
        </div>    
    )
}

export default Header